.container {
  text-align: center;
  display: flex;
  flex: 1;
  justify-content: center;
  width: 100%;
  min-height: 50%;
}

.App {
  background-image: url("https://cdn.pixabay.com/photo/2016/02/04/18/45/wall-1179614_960_720.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
  margin: 0;
  font-size: 2em;
  width: auto;
  height: 100vh;
  overflow: hidden;
}

.title {
  font-family: "Droid Sans" sans-serif;
  color: #f5f5f5;
  background-color: rgba(129, 118, 121, 0.767);
  margin-top: 0.3em;
  margin-bottom: 5vh;
  font-weight: bold;
  font-size: 2.2em;
}

.foot {
  position: absolute;
  left: 50%;
  bottom: 5px;
  margin-right: -50%;
  transform: translate(-50%, 0%);
  font-family: "Cantarell";
  color: #312f2f;
}

.clock1 {
  width: 30vw;
  height: 30vw;
  box-sizing: border-box;
  z-index: 100;
  position: relative;
  margin-right: 2px;
  border: 20px solid whitesmoke;
  border-radius: 50%;
  padding: 2rem;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.1), inset 0 0 0 3px whitesmoke,
    inset 0 0 10px black, 0 0 10px rgba(0, 0, 0, 0.2);
}

.clock2 {
  width: 30vw;
  height: 30vw;
  box-sizing: border-box;
  position: relative;
  border: 16px solid #412f32;
  border-radius: 50%;
  padding: 2rem;
  z-index: 0;
  box-shadow: 0 0 0 4px #3f2d30, inset 0 0 0 4px #3f2d30, inset 0 0 4px #3f2d30,
    0 0 4px #3f2d30;
}

.clock-face {
  position: relative;
  width: 100%;
  height: 100%;
}

.hand {
  height: 50%;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform-origin: 50% 100%;
  transition: all 0.05s;
  transition-timing-function: cubic-bezier(0.26, 2.74, 0.82, 0.07);
}

.hour-hand-1 {
  height: 35%;
  width: 8px;
  background-color: black;
}

.min-hand-1 {
  background-color: seagreen;
  width: 6px;
  height: 45%;
}

.second-hand-1 {
  background-color: palegoldenrod;
  width: 4px;
  transform: rotate(5deg);
}

.hour-hand-2 {
  height: 35%;
  width: 8px;
  background-color: #412f32;
}

.min-hand-2 {
  background-color: #412f32;
  width: 6px;
  height: 45%;
}

.second-hand-2 {
  background-color: #412f32;
  width: 4px;
  transform: rotate(5deg);
}

@-webkit-keyframes disappear {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes disappear {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.disappear {
  -webkit-animation-duration: 30s;
  animation-duration: 30s;
  -webkit-animation-name: disappear;
  animation-name: disappear;
}

@-webkit-keyframes hit {
  from,
  to {
    -webkit-transform: translateX(0) scale(1);
    transform: translateX(0) scale(1);
  }

  10% {
    -webkit-transform: translateX(-2px) scale(0.98);
    transform: translateX(-2px) scale(0.98);
  }

  20% {
    -webkit-transform: translateX(3px) scale(1.02);
    transform: translateX(3px) scale(1.02);
  }

  30% {
    -webkit-transform: translateX(-3px) scale(0.98);
    transform: translateX(-3px) scale(0.98);
  }

  40% {
    -webkit-transform: translateX(3px) scale(1.02);
    transform: translateX(3px) scale(1.02);
  }

  50% {
    -webkit-transform: translateX(-3px) scale(0.98);
    transform: translateX(-3px) scale(0.98);
  }

  60% {
    -webkit-transform: translateX(3px) scale(1.01);
    transform: translateX(3px) scale(1.01);
  }

  70% {
    -webkit-transform: translateX(-3px) scale(0.99);
    transform: translateX(-3px) scale(0.99);
  }

  80% {
    -webkit-transform: translateX(2px) scale(1.01);
    transform: translateX(2px) scale(1.01);
  }

  90% {
    -webkit-transform: translateX(-2px) scale(0.99);
    transform: translateX(-2px) scale(0.99);
  }
}

@keyframes hit {
  from,
  to {
    -webkit-transform: translateX(0) scale(1);
    transform: translateX(0) scale(1);
  }

  10% {
    -webkit-transform: translateX(-2px) scale(0.98);
    transform: translateX(-2px) scale(0.98);
  }

  20% {
    -webkit-transform: translateX(3px) scale(1.02);
    transform: translateX(3px) scale(1.02);
  }

  30% {
    -webkit-transform: translateX(-3px) scale(0.98);
    transform: translateX(-3px) scale(0.98);
  }

  40% {
    -webkit-transform: translateX(3px) scale(1.02);
    transform: translateX(3px) scale(1.02);
  }

  50% {
    -webkit-transform: translateX(-3px) scale(0.98);
    transform: translateX(-3px) scale(0.98);
  }

  60% {
    -webkit-transform: translateX(3px) scale(1.01);
    transform: translateX(3px) scale(1.01);
  }

  70% {
    -webkit-transform: translateX(-3px) scale(0.99);
    transform: translateX(-3px) scale(0.99);
  }

  80% {
    -webkit-transform: translateX(2px) scale(1.01);
    transform: translateX(2px) scale(1.01);
  }

  90% {
    -webkit-transform: translateX(-2px) scale(0.99);
    transform: translateX(-2px) scale(0.99);
  }
}

.hit {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  animation-fill-mode: backwards;
  -webkit-animation-fill-mode: backwards;
  -webkit-animation-name: hit;
  animation-name: hit;
}

@-webkit-keyframes flipOpen {
  from {
    -webkit-transform: rotateY(0);
    transform: rotateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }

  45% {
    -webkit-transform: rotateY(20deg);
    transform: rotateY(20deg);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }

  50% {
    -webkit-transform: rotateY(85deg);
    transform: rotateY(85deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  65% {
    -webkit-transform: rotateY(110deg);
    transform: rotateY(110deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  70% {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  80% {
    -webkit-transform: rotateY(170deg);
    transform: rotateY(170deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  85% {
    -webkit-transform: rotateY(100deg);
    transform: rotateY(100deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  93% {
    -webkit-transform: rotateY(85deg);
    transform: rotateY(85deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  96% {
    -webkit-transform: rotateY(10deg);
    transform: rotateY(10deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  to {
    -webkit-transform: rotateY(0deg);
    transform: rotateY(0deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
}

@keyframes flipOpen {
  from {
    -webkit-transform: rotateY(0);
    transform: rotateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }

  45% {
    -webkit-transform: rotateY(20deg);
    transform: rotateY(20deg);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }

  50% {
    -webkit-transform: rotateY(85deg);
    transform: rotateY(85deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  65% {
    -webkit-transform: rotateY(110deg);
    transform: rotateY(110deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  70% {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  80% {
    -webkit-transform: rotateY(170deg);
    transform: rotateY(170deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  85% {
    -webkit-transform: rotateY(100deg);
    transform: rotateY(100deg);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }

  93% {
    -webkit-transform: rotateY(85deg);
